<template>
    <div class="container_section-wrapper">
        <div class="container_section">
            <div class="section__head">
                <h2 class="section__title section__title_bd">Оборудование которое мы обслуживаем</h2>
            </div>
            <div class="equipment__kinds" :class="currentTab.color">
                <div class="equipment__kinds__main">
                    <div class="equipment__kinds__main__current equipment__kinds__current-tab">
                        <span>{{ listCur[0].name }}</span>
                    </div>
                    <div class="equipment__kinds__main__items">
                        <a href="#" v-for="(tab, index) in listCur" @click.prevent="setListKinds(index); reInitSwiper();"
                           class="equipment__kinds__main__item"
                           :data-value="tab.name"
                           :class="(currentTab.name == tab.name)?'active':''"
                        >
                            <span v-html="tab.icon"></span>
                            {{tab.name}}
                        </a>
                    </div>
                </div>
                <div class="equipment__kinds__detailed">
                    <div class="equipment__kinds__detailed__current equipment__kinds__current-tab">
                        <span>{{ listCur[0].children[0] }}</span>
                    </div>
                    <ul>
                        <li v-for="listItem in listKinds"
                            @click="addListItemToCurrent(listItem)"
                            :class="(currentListItem.name == listItem.name)?'active':''"
                        >
                            <a href="#"
                               :data-value="listItem.name"
                               @click.prevent="reInitSwiper">
                                {{listItem.name}}</a>
                        </li>
                    </ul>
                </div>
                <div class="equipment__kinds__content">
                  
                        <div class="equipment__kinds__content__items__slider-wrapper slider_main-wrapper">
                              <div class="equipment__kinds__content__items">
                        <div class="equipment__kinds__content__head">
                            <h4>Оборудование</h4>
                            <a v-bind:href="this.postInfo[0].main_link" class="link_default">
                                Узнать подробнее
                                <svg class="icon icon-arrow icon-arrow-long">
                                    <use xlink:href="#icon-arrow_long"></use>
                                </svg>
                                <svg class="icon icon-arrow-short">
                                    <use xlink:href="#icon-arrow"></use>
                                </svg>
                            </a>
                        </div>
                            <swiper class="equipment__kinds__content__items__slider" ref="awesomeSwiperA" :options="swiperOptionA">

                                <swiper-slide
                                    class="equipment__kinds__content__item-wrapper"
                                    v-for="productsItem in currentListItem.children"
                                    v-bind:key="productsItem.id"
                                >
                                    <div class="equipment__kinds__content__item">
                                        <div class="equipment__kinds__content__item__img-wrapper">
                                            <a :href="productsItem.link">
                                                <img
                                                    :src="productsItem.image"
                                                    alt="1"
                                                >
                                            </a>
                                        </div>

                                        <div class="equipment__kinds__content__item__title">
                                            {{productsItem.name}}
                                        </div>
                                    </div>
                                </swiper-slide>

                                <div class="slider__dots_main"  slot="pagination"></div>
                            </swiper>
                            <div class="equipment__kinds__content__items__arrow equipment__kinds__content__items__arrow-prev slider__arrow_main slider__arrow_main-prev">
                                <svg class="icon icon-arrow">
                                    <use xlink:href="#icon-arrow"></use>
                                </svg>
                            </div>
                            <div class="equipment__kinds__content__items__arrow equipment__kinds__content__items__arrow-next slider__arrow_main slider__arrow_main-next">
                                <svg class="icon icon-arrow">
                                    <use xlink:href="#icon-arrow"></use>
                                </svg>
                            </div>
                        </div>
                    </div>
                    <div class="equipment__kinds__content__manufacturers">
                        <div class="equipment__kinds__content__head">
                            <h4>Производители</h4>
                            <a href="http://tomt.rocketcompany.website/proizvoditeli/" class="link_default">
                                Все производители
                                <svg class="icon icon-arrow icon-arrow-long">
                                    <use xlink:href="#icon-arrow_long"></use>
                                </svg>
                                <svg class="icon icon-arrow-short">
                                    <use xlink:href="#icon-arrow"></use>
                                </svg>
                            </a>
                        </div>

                     
                        <div class="equipment__kinds__content__manufacturers__items">
                            <template v-for="brandItem in currentListItem.manufacturers">
                                <template v-for="brand in brandItem">

                                    <img :src="brand" alt="1">

                                </template>
                            </template>
                        </div>
                    </div>
                </div>
            </div>


        </div>
    </div>
</template>
<script>
    import Swiper from "swiper";

    export default {
        name: "FullEquipment",
        props:['postInfo'],
        data() {
            return {
                swiperOptionA: {
                    slidesPerView: 3,
                    spaceBetween: 20,
                    loop: false,
                    breakpoints: {
                        768: {
                            slidesPerView:2
                        },
                        480: {
                            slidesPerView: 1
                        },
                    },
                    pagination: {
                        el: '.slider__dots_main',
                        clickable: true
                    },
                    navigation: {
                        nextEl: '.equipment__kinds__content__items__arrow-next',
                        prevEl: '.equipment__kinds__content__items__arrow-prev'
                    }
                },
                currentTab: [],
                listKinds: [],
                currentListItem: {},
                listCur: {}
            }
        },
        created() {
            this.setListKinds(0);
             this.listCur = this.postInfo;
        },
        computed: {
            swiperA() {
                return this.$refs.awesomeSwiperA.swiper;
            }
        },
        methods: {
            setListKinds(index) {
                this.currentTab = this.postInfo[index];
                this.listKinds = this.postInfo[index].children;
                this.currentListItem = this.postInfo[index].children[0];
                this.currentItem = this.postInfo[index];
            },
            addListItemToCurrent(elem) {
                this.currentListItem = elem;
            },
            reInitSwiper(){
                 this.$refs.awesomeSwiperA.swiper.destroy(false,true);
                this.$refs.awesomeSwiperA.swiper = new Swiper('.equipment__kinds__content__items__slider', this.swiperOptionA);
            }
        }
    }
</script>