<template>
    <div class="container_section-wrapper">
        <div class="container_section">
            <div class="equipment__kinds">
                <div class="equipment__kinds__detailed">
                    <div class="equipment__kinds__detailed__current equipment__kinds__current-tab">
                        <span>{{ listCur[0].name }}</span>
                    </div>
                    <ul>
                        <li
                                v-for="(listItem, index) of listCur"
                                @click.prevent="addListItemToCurrent(listItem)"
                                :class="(currentListItem.name == listItem.name)?'active':''"
                                :key="index"
                        >
                            <a
                                    href="#"
                                    :data-value="listItem.name"
                                    @click.prevent="reInitSwiperC"
                            >
                                {{ listItem.name }}
                                <span>{{ listItem.children.length }} товара</span>
                            </a>
                        </li>
                    </ul>
                </div>

                <div class="equipment__kinds__content">
                    <div
                            class="equipment__kinds__content__slider-wrapper slider_main-wrapper"
                            v-if="listCur.length"
                    >
                        <swiper
                                class="equipment__kinds__content__slider"
                                ref="awesomeSwiperC"
                                :options="swiperOptionC"
                        >
                            <swiper-slide
                                    class="equipment__kinds__content__cart-wrapper"
                                    v-for="item in currentListItem.children"
                                    v-bind:key="item.id"
                            >
                                <div class="equipment__kinds__content__cart">
                                    <div class="equipment__kinds__content__cart__img-wrapper">
                                        <div class="equipment__kinds__content__cart__img-wrapper__main">
                                            <img
                                                    :src="currentImgCart"
                                                    :alt="item.name"
                                            >
                                            <!--<img-->
                                            <!--:src="defaultImg"-->
                                            <!--&gt;-->
                                        </div>
                                        <div class="equipment__kinds__content__cart__img-wrapper__items">
                                            <template v-if="item.image.length">
                                                <div
                                                        class="equipment__kinds__content__cart__img-wrapper__item"
                                                        v-for="img in item.image"
                                                >
                                                    <img
                                                            :src="img.image"
                                                            :alt="item.name"
                                                            @click="addImgCartToCurrent(img.image)"
                                                    >
                                                </div>
                                            </template>
                                            <template v-else>
                                                <div
                                                        class="equipment__kinds__content__cart__img-wrapper__item"
                                                >
                                                    <img
                                                            :src="defaultImg"
                                                            :alt="item.name"
                                                    >
                                                </div>
                                            </template>
                                        </div>
                                    </div>
                                    <div class="equipment__kinds__content__cart__info">
                                        <div class="equipment__kinds__content__cart__info__name">
                                            {{ item.name }}
                                        </div>

                                        <div class="equipment__kinds__content__cart__info__charac">
                                            <div class="equipment__kinds__content__cart__info__charac__item">
                                                <div>Производитель:</div>

                                                <div class="equipment__kinds__content__cart__info__charac__item__val">
                                                    {{ item.manufacturer }}
                                                </div>
                                            </div>
                                            <div class="equipment__kinds__content__cart__info__charac__item">
                                                <div>Год производства:</div>

                                                <div class="equipment__kinds__content__cart__info__charac__item__val">
                                                    {{ item.year }}
                                                </div>
                                            </div>
                                            <div class="equipment__kinds__content__cart__info__charac__item">
                                                <div>В эксплуатации был:</div>

                                                <div class="equipment__kinds__content__cart__info__charac__item__val">
                                                    {{ item.exploitation }}
                                                </div>
                                            </div>
                                        </div>
                                        <div class="equipment__kinds__content__cart__info__components">
                                            <div class="equipment__kinds__content__cart__info__title">В наличие
                                                датчики:
                                            </div>
                                            <ul>
                                                <li v-for="n in item.components">
                                                    <svg class="icon">
                                                        <use xlink:href="#icon-check-mark"></use>
                                                    </svg>
                                                    {{ n.specification }}
                                                </li>
                                            </ul>
                                        </div>
                                        <div class="equipment__kinds__content__cart__info__action">
                                            <div class="equipment__kinds__content__cart__info__action__price">
                                                <div class="equipment__kinds__content__cart__info__title">Цена:</div>
                                                <span>{{ item.price }}</span>
                                            </div>
                                            <a href="#" class="btn btn_info_full-bg getOrderModal-js"
                                               :data-id="item.id"
                                            >
                                                Заказть
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </swiper-slide>
                            <div class="slider__dots_main" slot="pagination"></div>
                        </swiper>
                        <div class="equipment__kinds__content__items__arrow equipment__kinds__content__items__arrow-prev slider__arrow_main slider__arrow_main-prev">
                            <svg class="icon icon-arrow">
                                <use xlink:href="#icon-arrow"></use>
                            </svg>
                        </div>
                        <div class="equipment__kinds__content__items__arrow equipment__kinds__content__items__arrow-next slider__arrow_main slider__arrow_main-next">
                            <svg class="icon icon-arrow">
                                <use xlink:href="#icon-arrow"></use>
                            </svg>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Swiper from "swiper";

    export default {
        name: "SaleEquipment",
        props: ['postInfo'],
        data() {
            return {
                swiperOptionC: {
                    slidesPerView: 1,
                    loop: false,
                    pagination: {
                        el: '.slider__dots_main',
                        clickable: true
                    },
                    navigation: {
                        nextEl: '.equipment__kinds__content__items__arrow-next',
                        prevEl: '.equipment__kinds__content__items__arrow-prev'
                    }
                },
                currentListItem: {},
                currentImgCart: {},
                listCur: {},
                listCurIndex: 0,
                curChildrenIndex: 0,
                defaultImg: '../wp-content/themes/tomt/assets/img/icons/icon-no-image.png'
            }
        },
        created() {
            this.setListKinds(0);
            this.setImgCart(0, 0);
            this.listCur = this.postInfo;
        },
        mounted() {
            let arrowPrevSlider = document.querySelector('.equipment__kinds__content__items__arrow-prev');
            let arrowNextSlider = document.querySelector('.equipment__kinds__content__items__arrow-next');
            let listKinds = document.querySelectorAll('.equipment__kinds__detailed ul li');

            arrowPrevSlider.addEventListener('click', () => {
                if (this.curChildrenIndex > 0) {
                    this.curChildrenIndex--;
                    this.setImgCart(this.listCurIndex, this.curChildrenIndex)
                }
            });

            arrowNextSlider.addEventListener('click', () => {
                if (this.currentListItem.children.length - 1 > this.curChildrenIndex) {
                    this.curChildrenIndex++;
                    this.setImgCart(this.listCurIndex, this.curChildrenIndex)
                }
            });

            listKinds.forEach((el, index) => {
                el.addEventListener('click', () => {
                    this.listCurIndex = index;
                    this.reInitSwiperC();
                });
            });
        },
        computed: {
            swiperC() {
                return this.$refs.awesomeSwiperC.swiper;
            }
        },
        methods: {
            setListKinds(index) {
                this.currentListItem = this.postInfo[index];
            },
            addListItemToCurrent(elem) {
                this.currentListItem = elem;
            },
            setImgCart(index, childrenIndex) {
                if (this.postInfo[index].children[childrenIndex].image.length >= 1) {
                    this.currentImgCart = this.postInfo[index].children[childrenIndex].image[0].image;
                } else {
                    this.currentImgCart = this.defaultImg
                }
            },
            addImgCartToCurrent(elem) {
                this.currentImgCart = elem;
            },
            reInitSwiperC() {
                this.$refs.awesomeSwiperC.swiper.destroy(false, true);
                this.$refs.awesomeSwiperC.swiper = new Swiper('.equipment__kinds__content__slider', this.swiperOptionC);
                this.setImgCart(this.listCurIndex, 0);
            }
        }
    }
</script>