                            >
                                {{currentItemListKinds.catSection.name}}
                                <svg class="icon icon-arrow">
                                    <use xlink:href="#icon-arrow"></use>
                                </svg>
                            </span>
                            <ul v-if="statusListKinds">
                                <li v-for="item in listKinds"
                                    @click="[(statusListKinds) ? statusListKinds = false : statusListKinds = true, currentItemListKinds.catSection = item]"
